import React from "react";

const TextSection = ({
  text,
  classNameText,
  title,
  classNameTitle,
  background = "rgba(0, 0, 0, 0.7) ",
  color = "white",
  className,
  Children,
}) => {
  return (
    <div
      style={{
        textAlign: "center",
        background,
        color,
        borderRadius: "15px",
      }}
      className={`py-2 py-md-5 px-md-3 px-1 ${className}`}
    >
      {title && <p className={` ${classNameTitle} `}>{title} </p>}
      <p className={` ${classNameText}`}>{text}</p>
      {Children && Children}
    </div>
  );
};
export default TextSection;
