import React, { useState } from "react";
import { useHistory } from "react-router-dom";
import "./ButtonAnimate.scss";
import { GiClick } from "react-icons/gi";

const ButtonAnimate = ({ title = "test", to = "/", className = "" }) => {
  const [animateClasse, setAnimateClasse] = useState("");
  const { push } = useHistory();

  const animateButton = (e) => {
    e.preventDefault();
    setAnimateClasse("animate");
    setTimeout(() => {
      setAnimateClasse("");
      push(to);
    }, 700);
  };

  return (
    <div className={className}>
      <button
        className={`bubbly-button rounded text-white ${animateClasse}`}
        onClick={(e) => animateButton(e)}
      >
        {title}
        <GiClick className="mx-2" />
      </button>
    </div>
  );
};

export default ButtonAnimate;
