import React from "react";
import "./ImageWithMask.css";

const ImageWithMask = ({ image, mask }) => {
  return (
    <img
      src={image}
      alt="robe"
      style={
        mask && {
          WebkitMaskImage: `url(${mask})`,
        }
      }
      className="mask p-1 rounded"
    />
  );
};

export default ImageWithMask;
